import React from "react";
import "./InfoCard.css";
import { Row, Col } from "antd";
import chibi3 from "../../Images/chibi_screenshot.png";
import chibi2 from "../../Images/chibi_screenshot (2).png";
import chibi1 from "../../Images/M4_SOPMOD_II.png";
import frame from "../../Images/imageFrame.png";
import gif1 from "../../Images/M4_SOPMOD_II.gif";
import gif2 from "../../Images/AR15.gif";
import gif3 from "../../Images/M4A1.gif";

let img = {
  1: { portrait: chibi1, gif: gif1 },
  2: { portrait: chibi2, gif: gif2 },
  3: { portrait: chibi3, gif: gif3 },
};

const InfoCard = ({ InfoCardContent, handleAdjustStatsButton }) => {
  const { name, stats, id } = InfoCardContent;

  const onDragStart = (e, id, info) => {
    e.dataTransfer.setData("id", id);
    e.dataTransfer.setData("doll", info);
    let image = new Image();
    image.src = img[id].portrait;
    console.log(image.src);
    e.dataTransfer.setDragImage(image, 125, 125);
  };

  return stats.length ? (
    <div
      className="box"
      draggable
      onDragStart={(e) => onDragStart(e, id, name)}
    >
      <div className="box-img">
        <img
          className="img-frame"
          src="https://en.gfwiki.com/images/2/2c/Infobox_border.png"
        />
        {img[id] ? <img className="img-doll" src={img[id].portrait} /> : <></>}

        <div className="id">{id}</div>
      </div>

      <div className="content">
        <>
          {name}
          <div className="content-table">
            <Row justify="space-between">
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    HP
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[0][1]}
                  </Col>
                </Row>
              </Col>
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    FP
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[1][1]}
                  </Col>
                </Row>
              </Col>
            </Row>

            <Row justify="space-between">
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    ROF
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[5][1]}
                  </Col>
                </Row>
              </Col>
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    ACC
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[2][1]}
                  </Col>
                </Row>
              </Col>
            </Row>

            <Row justify="space-between">
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    EVA
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[3][1]}
                  </Col>
                </Row>
              </Col>
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    SPD
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[4][1]}
                  </Col>
                </Row>
              </Col>
            </Row>

            <Row justify="space-between">
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    AP
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[6][1]}
                  </Col>
                </Row>
              </Col>
              <Col span={8}>
                <Row justify="space-between">
                  <Col span={12} style={{ textAlign: "left" }}>
                    CR
                  </Col>
                  <Col span={12} style={{ textAlign: "right" }}>
                    {stats[7][1]}
                  </Col>
                </Row>
              </Col>
            </Row>
          </div>
        </>
      </div>
      <div className="button">
        <a className="b1" onClick={handleAdjustStatsButton}>
          Adjust Stats
        </a>
      </div>
    </div>
  ) : (
    <></>
  );
};

export default InfoCard;
