import React, { Component } from "react";
import "./EchelonBoard.css";
import tile from "../../Images/boardTile.png";
import chibi1 from "../../Images/M4_SOPMOD_II.png";
import chibi2 from "../../Images/chibi_screenshot (2).png";
import chibi3 from "../../Images/chibi_screenshot.png";
import gif1 from "../../Images/M4_SOPMOD_II.gif";
import gif2 from "../../Images/AR15.gif";
import gif3 from "../../Images/M4A1.gif";
import { Row, Col } from "antd";
import { DeleteOutlined } from "@ant-design/icons";
import DollTag from "./DollTag/DollTag";
let img = {
  1: { portrait: chibi1, gif: gif1 },
  2: { portrait: chibi2, gif: gif2 },
  3: { portrait: chibi3, gif: gif3 },
};

const board = [];
for (let row = 0; row < 3; row++) {
  const currentRow = [];
  for (let col = 0; col < 3; col++) {
    currentRow.push("");
  }
  board.push(currentRow);
}

const displayedDolls = {};
class EchelonBoard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      board: board,
    };
  }

  onDragStart = (e, id, idx1, idx2) => {
    e.dataTransfer.setData("board", id);
    e.dataTransfer.setData("srcRow", idx1);
    e.dataTransfer.setData("srcCol", idx2);
  };

  onDrop = (ev, idx1, idx2) => {
    let id = ev.dataTransfer.getData("id");
    let name = ev.dataTransfer.getData("doll");
    let idInBoard = ev.dataTransfer.getData("board");
    let srcRow = ev.dataTransfer.getData("srcRow");
    let srcCol = ev.dataTransfer.getData("srcCol");
    let arr = this.state.board;
    let emptyTiles = 0;

    if (name) displayedDolls[id] = name;

    if (idx1 === -1 && idx2 === -1 && !id) {
      arr[srcRow][srcCol] = "";
      this.setState({ board: arr });
      return;
    }
    if (idInBoard && idx1 !== -1 && idx2 !== -1) {
      for (const row of arr) {
        for (const tile of row) if (tile === "") emptyTiles++;
      }

      if (arr[idx1][idx2])
        [arr[srcRow][srcCol], arr[idx1][idx2]] = [
          arr[idx1][idx2],
          arr[srcRow][srcCol],
        ];
      else {
        arr[srcRow][srcCol] = "";
        arr[idx1][idx2] = idInBoard;
      }
      this.setState({ board: arr });
    } else if (idx1 !== -1 && idx2 !== -1) {
      let taken = false;
      for (const row of arr) {
        for (const tile of row) {
          if (tile === "") emptyTiles++;
          if (tile === id) taken = true;
        }
      }
      if (emptyTiles >= 5 && !taken) arr[idx1][idx2] = id;

      this.setState({ board: arr });
    }
  };

  render() {
    return (
      <Row
        className="board-container"
        justify="start"
        style={{ height: "100%" }}
      >
        <Col>
          <div className="board">
            {this.state.board.map((row, idx1) => (
              <Row>
                {row.map((col, idx2) => (
                  <Col
                    span={8}
                    className="droppable"
                    onDragOver={(e) => e.preventDefault()}
                    onDrop={(e) => this.onDrop(e, idx1, idx2)}
                  >
                    <img src={tile} className="tile" />
                    {col ? (
                      <img
                        draggable
                        onDragStart={(e) =>
                          this.onDragStart(e, col, idx1, idx2)
                        }
                        src={img[col].gif}
                        alt={""}
                        style={{
                          position: "absolute",
                          width: "calc((90vh - 40px) / 3)",
                          height: "calc((90vh - 40px) / 3)",
                          top: 0,
                          left: 0,
                        }}
                      />
                    ) : (
                      <></>
                    )}
                  </Col>
                ))}
              </Row>
            ))}
          </div>
        </Col>
        <Col className="side-board">
          <Row>
            <Col className="status-box">
              {this.state.board.map((row, idx1) =>
                row.map((tile, idx2) =>
                  tile ? (
                    <DollTag
                      name={displayedDolls[tile]}
                      position={(2 - idx1) * 3 + (idx2 + 1)}
                    />
                  ) : (
                    <></>
                  )
                )
              )}
            </Col>
          </Row>
          <Row>
            <Col className="delete-box">
              <DeleteOutlined
                className="delete-icon"
                onDragOver={(e) => e.preventDefault()}
                onDrop={(e) => this.onDrop(e, -1, -1)}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }
}

export default EchelonBoard;
